import axios from "axios";
import moment from "moment";
import { userConstants } from "../constants";

export const changeActiveProfile = (type, name, typeUser) => {
  return async function (dispatch, getState) {
    const state = getState().userState;
    if (type === "rasp") {
      const profiles = localStorage.getItem("profiles");
      const profilesObj = JSON.parse(profiles);
      profilesObj.activeRasp = { name, type: typeUser };
      localStorage.setItem("profiles", JSON.stringify(profilesObj));
      dispatch({
        type: userConstants.SET_ACTIVE_RASP,
        payload: { name, type: typeUser },
      });

      const reqWeek = state.raspData.reqWeek;
      const data = await axios
        .get(`${process.env.REACT_APP_URL}/rasp`, {
          params: { active: { type: typeUser, name }, reqWeek },
        })
        .then((res) => res.data);

      dispatch({
        type: userConstants.SET_REQWEEK_DATA,
        payload: {
          raspData: data,
          dayofweek: moment().format("e"),
          loading: true,
        },
      });
    }
    if (type === "buse") {
      const profiles = localStorage.getItem("profiles");
      const profilesObj = JSON.parse(profiles);
      profilesObj.activeBuse = { name };
      localStorage.setItem("profiles", JSON.stringify(profilesObj));
      dispatch({ type: userConstants.SET_ACTIVE_BUSE, payload: name });
    }
  };
};

export const changeValue = (value, type) => {
  if (type === "group") {
    if (value && value !== "Выбрать группу") {
      return {
        type: userConstants.SET_GROUP_VALUE,
        payload: { value, btn: true },
      };
    } else {
      return {
        type: userConstants.SET_GROUP_VALUE,
        payload: { value, btn: false },
      };
    }
  }
  if (type === "teacher") {
    if (value && value !== "Выбрать преподавателя") {
      return {
        type: userConstants.SET_TEACHER_VALUE,
        payload: { value, btn: true },
      };
    } else {
      return {
        type: userConstants.SET_TEACHER_VALUE,
        payload: { value, btn: false },
      };
    }
  }
};

export const addUserProfile = (type, name) => {
  return async function (dispatch) {
    let user = JSON.parse(localStorage.getItem("user"));
    let profiles = JSON.parse(localStorage.getItem("profiles"));

    if (user) {
      const status = profiles.rasp.findIndex((el) => el.name === name);
      if (status !== -1) {
        profiles.rasp.splice(status, 1);
      }
      profiles.rasp.push({ type, name });
      profiles.activeRasp = { type, name };
      localStorage.setItem(
        "profiles",
        JSON.stringify({
          ...profiles,
          rasp: profiles.rasp,
          activeRasp: { type, name },
        })
      );
    }

    if (!user) {
      user = { type, name };
      profiles.rasp = [{ type, name }];
      profiles.activeRasp = { type, name };
      localStorage.setItem(
        "profiles",
        JSON.stringify({
          ...profiles,
          rasp: [{ type, name }],
          activeRasp: { type, name },
        })
      );
      localStorage.setItem("user", JSON.stringify({ type, name }));
    }

    dispatch({
      type: userConstants.ADD_USER_PROFILE,
      payload: { user, profiles },
    });

    const reqWeek = moment().isoWeekday(1).format("YYYY-MM-DD");
    const data = await axios
      .get(`${process.env.REACT_APP_URL}/rasp`, {
        params: { active: { type, name }, reqWeek },
      })
      .then((res) => res.data);

    dispatch({
      type: userConstants.SET_REQWEEK_DATA,
      payload: {
        raspData: data,
        dayofweek: moment().format("e"),
        loading: true,
      },
    });
  };
};

export const addHeltsName = (name, buse, dir) => {
  const helts = JSON.parse(localStorage.getItem("busesHelts"));
  const itemIndex = helts[buse][dir].indexOf(name);
  if (itemIndex !== -1) {
    helts[buse][dir].splice(itemIndex, 1);
  } else {
    helts[buse][dir].push(name);
  }
  localStorage.setItem("busesHelts", JSON.stringify(helts));
  return {
    type: userConstants.ADD_HELTS_NAME,
    payload: helts,
  };
};

export const deleteUserProfile = (deleteItem) => {
  return async function (dispatch) {
    const profiles = JSON.parse(localStorage.getItem("profiles"));
    const index = profiles.rasp.findIndex((el) => el.name === deleteItem);
    profiles.rasp.splice(index, 1);
    if (profiles.rasp.length === 0) {
      profiles.rasp = null;
      profiles.activeRasp = null;
      localStorage.setItem("profiles", JSON.stringify(profiles));
      localStorage.setItem("user", 0);
      return dispatch({
        type: userConstants.DELETE_USER_PROFILE,
        payload: { profiles, loading: true },
      });
    }
    if (index === 0) {
      profiles.activeRasp = profiles.rasp[0];
    }
    if (index === profiles.rasp.length) {
      profiles.activeRasp = profiles.rasp[index - 1];
    }
    if (index !== 0 && index !== profiles.rasp.length) {
      profiles.activeRasp = profiles.rasp[index];
    }
    localStorage.setItem("profiles", JSON.stringify(profiles));

    dispatch({
      type: userConstants.DELETE_USER_PROFILE,
      payload: { profiles, loading: false },
    });

    const reqWeek = moment().isoWeekday(1).format("YYYY-MM-DD");
    const data = await axios
      .get(`${process.env.REACT_APP_URL}/rasp`, {
        params: {
          active: {
            type: profiles.activeRasp.type,
            name: profiles.activeRasp.name,
          },
          reqWeek,
        },
      })
      .then((res) => res.data);

    dispatch({
      type: userConstants.SET_REQWEEK_DATA,
      payload: {
        raspData: data,
        dayofweek: moment().format("e"),
        loading: true,
      },
    });
  };
};

export const checkUser = () => {
  return async function (dispatch, getState) {
    const user = JSON.parse(localStorage.getItem("user"));

    const dayofweek = +moment().format("e") !== 0 ? moment().format("e") : "1";

    const initProfiles = {
      buse: [
        { name: 6 },
        { name: 14 },
        { name: 29 },
        { name: 31 },
        { name: 32 },
      ],
      activeBuse: { name: 6 },
    };

    const initHelts = {
      6: {
        0: ["Университет"],
        1: ["Сквер героя Карвата"],
        dir: [0, 1],
      },
      14: {
        0: ["Университет"],
        1: ["Стадион"],
        dir: [0, 1],
      },
      29: {
        0: ["Университет"],
        1: ["Стадион"],
        dir: [0, 1],
      },
      31: {
        0: ["Сквер героя Карвата"],
        1: ["Университет"],
        dir: [0, 1],
      },
      32: {
        0: ["Университет"],
        dir: [0],
      },
    };

    const buse = await axios
      .get(`${process.env.REACT_APP_URL}/buses`)
      .then((res) => {
        const buses = res.data;
        const busesObj = {};
        for (let i = 0; i < buses.length; i++) {
          busesObj[buses[i].number] = buses[i];
        }
        return busesObj;
      });
    const gptc = await axios
      .get(`${process.env.REACT_APP_URL}/gptc`)
      .then((res) => {
        const { groups, teachers } = res.data;
        const groupsData = {
          list: ["Выбрать группу", ...groups.map((item) => item.group).sort()],
          value: "Выбрать группу",
        };
        const teachersData = {
          list: [
            "Выбрать преподавателя",
            ...teachers.map((item) => item.teacher).sort(),
          ],
          value: "Выбрать преподавателя",
        };
        return { groupsData, teachersData };
      });

    localStorage.setItem("buses", JSON.stringify(buse));
    localStorage.setItem("groupsData", JSON.stringify(gptc.groupsData));
    localStorage.setItem("teachersData", JSON.stringify(gptc.teachersData));

    if (user) {
      const profiles = JSON.parse(localStorage.getItem("profiles"));
      const helts = JSON.parse(localStorage.getItem("busesHelts"));

      dispatch({
        type: userConstants.SET_INIT_STATE,
        payload: {
          user,
          profiles: { ...profiles, ...initProfiles },
          helts: { ...helts },
          buse,
          ...gptc,
          dayofweek,
        },
      });

      const reqWeek =
        +moment().format("e") !== 0
          ? moment().isoWeekday(1).format("YYYY-MM-DD")
          : moment()
              .isoWeekday(1 + 7)
              .format("YYYY-MM-DD");
      const { type, name } = getState().userState.profiles.activeRasp;
      const data = await axios
        .get(`${process.env.REACT_APP_URL}/rasp`, {
          params: { active: { type, name }, reqWeek },
        })
        .then((res) => res.data);

      dispatch({
        type: userConstants.SET_REQWEEK_DATA,
        payload: {
          raspData: data,
          dayofweek,
          loading: true,
        },
      });

      if (+moment().format("e") !== 0) {
        const nextweek_data = await axios
          .get(`${process.env.REACT_APP_URL}/rasp`, {
            params: {
              active: { type, name },
              reqWeek: moment()
                .isoWeekday(1 + 7)
                .format("YYYY-MM-DD"),
            },
          })
          .then((res) => res.data);
        dispatch({
          type: userConstants.SET_NEXTWEEK_RASP_STATUS,
          payload: nextweek_data,
        });
      }
    }

    if (!user) {
      localStorage.setItem("busesHelts", JSON.stringify(initHelts));
      localStorage.setItem("profiles", JSON.stringify(initProfiles));
      localStorage.setItem("user", 0);

      dispatch({
        type: userConstants.SET_INIT_STATE,
        payload: {
          user: null,
          profiles: initProfiles,
          helts: initHelts,
          buse,
          ...gptc,
          dayofweek,
        },
      });
    }

    saveIpUsers();
    async function saveIpUsers() {
      try {
        if (!user) return;
        const { data } = await axios
          .get(
            "https://ipgeolocation.abstractapi.com/v1/?api_key=f7f36c22e2104ab3a73de182b98e6705"
          )
          .catch((err) => {});
        if (data) {
          await axios.post(`${process.env.REACT_APP_URL}/save_ip`, {
            ...user,
            ip_address: data.ip_address,
          });
        }
      } catch (error) {}
    }
  };
};

export const reqUserRasp = (startDate, dayofweek) => {
  return async function (dispatch, getState) {
    const reqWeek = moment(startDate).format("YYYY-MM-DD");
    const { name, type } = getState().userState.profiles.activeRasp;
    const data = await axios
      .get(`${process.env.REACT_APP_URL}/rasp`, {
        params: { active: { type, name }, reqWeek },
      })
      .then((res) => res.data);

    return dispatch({
      type: userConstants.SET_REQWEEK_DATA,
      payload: { raspData: data, dayofweek, loading: true },
    });
  };
};

export const setNextweekData = () => {
  return async function (dispatch, getState) {
    const data = getState().userState.nextweekData;

    return dispatch({
      type: userConstants.SET_NEXT_WEEK_DATA,
      payload: data,
    });
  };
};
