import React, { useState, useEffect } from "react";

import Button from "@material-ui/core/Button";
import Dialog from "@material-ui/core/Dialog";
import DialogActions from "@material-ui/core/DialogActions";
import DialogContent from "@material-ui/core/DialogContent";
import ArrowBackIosIcon from "@material-ui/icons/ArrowBackIos";
import ArrowForwardIosIcon from "@material-ui/icons/ArrowForwardIos";
import DateRangeIcon from "@material-ui/icons/DateRange";
import ClearIcon from "@material-ui/icons/Clear";

import DatePicker from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";
import startOfWeek from "date-fns/startOfWeek";
import endOfWeek from "date-fns/endOfWeek";
import { getMonth, getYear } from "date-fns";

import moment from "moment";
import { IconButton, withStyles } from "@material-ui/core";
import { createStyles } from "@material-ui/styles";

import { reqUserRasp } from "../actions";
import { store } from "../store";

import { makeStyles } from "@material-ui/core/styles";
import Modal from "@material-ui/core/Modal";

import SelectUserForm from "./RaspSlider/SelectUserForm";

const useStyles = makeStyles((theme) => ({
  paper: {
    position: "absolute",
    top: "50%",
    left: "50%",
    width: "80%",
    transform: "translate(-50%, -50%)",
    backgroundColor: theme.palette.background.paper,
    boxShadow: theme.shadows[5],
    outline: "none",
    borderRadius: "12px",
    textAlign: "center",
  },
}));

const months = [
  "Январь",
  "Февраль",
  "Март",
  "Апрель",
  "Май",
  "Июнь",
  "Июль",
  "Август",
  "Сентябрь",
  "Октябрь",
  "Ноябрь",
  "Декабрь",
];

function ProfilesComponent({
  type,
  profiles,
  active,
  changeActiveProfile,
  deleteUserProfile,
}) {
  const classes = useStyles();
  const [dateModal, setDateModal] = useState(false);
  const [open, setOpen] = useState(false);

  const [numbOfDays, setNumbOfDays] = useState({
    now: moment().format("D"),
    month: moment().format("MM"),
    dayofweek: moment().format("e"),
    start: moment(startOfWeek(new Date(), { weekStartsOn: 1 })).format("D"),
    end: moment(endOfWeek(new Date(), { weekStartsOn: 1 })).format("D"),
  });
  const [startDate, setStartDate] = useState(
    startOfWeek(new Date(), { weekStartsOn: 1 })
  );
  const [endDate, setEndDate] = useState(
    endOfWeek(new Date(), { weekStartsOn: 1 })
  );

  useEffect(() => {
    const el = document.querySelector(".profiles .items .active");
    el.scrollIntoView({ inline: "center" });
  }, []);

  const handleClickOpen = () => setDateModal(true);
  const handleClose = () => setDateModal(false);

  const handleMaodalOpen = () => setOpen(true);
  const handleModalClose = () => setOpen(false);

  const handlePickDate = () => {
    store.dispatch(reqUserRasp(startDate, numbOfDays.dayofweek));
    handleClose();
  };

  const handleWeekChange = (date) => {
    setNumbOfDays({
      ...numbOfDays,
      now: moment(date).format("D"),
      month: moment(date).format("MM"),
      dayofweek: moment(date).format("e"),
    });
    setStartDate(startOfWeek(date, { weekStartsOn: 1 }));
    setEndDate(endOfWeek(date, { weekStartsOn: 1 }));
  };

  const renderWrappedWeekDay = (date, dateUtf) => {
    const isActive =
      date === +numbOfDays.now &&
      +numbOfDays.month === +moment(dateUtf).format("MM")
        ? true
        : false;
    return (
      <div className={`btn-wrapper ${isActive ? "active" : ""}`}>
        <span>{date}</span>
      </div>
    );
  };

  const renderCustomHeader = ({
    date,
    decreaseMonth,
    increaseMonth,
    prevMonthButtonDisabled,
    nextMonthButtonDisabled,
  }) => (
    <div className="react-datepicker__custom-header">
      <IconButton onClick={decreaseMonth} disabled={prevMonthButtonDisabled}>
        <ArrowBackIosIcon />
      </IconButton>

      <div className="month">
        {months[getMonth(date)]} {getYear(date)}
      </div>

      <IconButton onClick={increaseMonth} disabled={nextMonthButtonDisabled}>
        <ArrowForwardIosIcon />
      </IconButton>
    </div>
  );

  return (
    <div className="profiles">
      <div className="items">
        {profiles.map((item, i) => {
          const isActive = active.name === item.name;
          return (
            <div
              key={i}
              className={isActive ? "item active" : "item"}
              onClick={() => {
                changeActiveProfile(type, item.name, item.type);
              }}
            >
              {item.name}
              {type === "rasp" ? (
                <ClearIcon
                  onClick={(e) => {
                    if (e.stopPropagation) {
                      e.stopPropagation();
                    }
                    deleteUserProfile(item.name);
                  }}
                />
              ) : (
                ""
              )}
            </div>
          );
        })}
        {type === "rasp" ? (
          <div className="item add" onClick={handleMaodalOpen}>
            Добавить
          </div>
        ) : (
          ""
        )}
      </div>
      <div className="shadow"></div>
      {type === "rasp" ? (
        <div
          className="edit item"
          tabIndex="1"
          onClick={() => handleClickOpen(true)}
        >
          <DateRangeIcon />
        </div>
      ) : (
        ""
      )}

      <Dialog
        open={dateModal}
        onClose={handleClose}
        aria-labelledby="alert-dialog-title"
        aria-describedby="alert-dialog-description"
      >
        <DialogContent>
          <DatePicker
            className="calendar"
            selected={startDate}
            onChange={handleWeekChange}
            startDate={startDate}
            endDate={endDate}
            renderCustomHeader={renderCustomHeader}
            renderDayContents={renderWrappedWeekDay}
            calendarStartDay={1}
            inline
          />
        </DialogContent>
        <DialogActions>
          <Button onClick={handleClose}>Cancle</Button>
          <Button onClick={handlePickDate}>Select</Button>
        </DialogActions>
      </Dialog>

      <Modal open={open} onClose={handleModalClose}>
        <div className={classes.paper}>
          <SelectUserForm handleModalClose={handleModalClose} />
        </div>
      </Modal>
    </div>
  );
}

const styles = createStyles((theme) => ({
  dayWrapper: {
    position: "relative",
  },
  day: {
    width: 36,
    height: 36,
    fontSize: theme.typography.caption.fontSize,
    margin: "0 2px",
    color: "inherit",
  },
  customDayHighlight: {
    position: "absolute",
    top: 0,
    bottom: 0,
    left: "2px",
    right: "2px",
    border: `1px solid ${theme.palette.secondary.main}`,
    borderRadius: "50%",
  },
  nonCurrentMonthDay: {
    color: theme.palette.text.disabled,
  },
  highlightNonCurrentMonthDay: {
    color: "#676767",
  },
  highlight: {
    background: theme.palette.primary.main,
    color: theme.palette.common.white,
  },
  firstHighlight: {
    extend: "highlight",
    borderTopLeftRadius: "50%",
    borderBottomLeftRadius: "50%",
  },
  endHighlight: {
    extend: "highlight",
    borderTopRightRadius: "50%",
    borderBottomRightRadius: "50%",
  },
}));

export default withStyles(styles)(ProfilesComponent);
