import Autocomplete from "@material-ui/lab/Autocomplete";
import TextField from "@material-ui/core/TextField";
import Button from "@material-ui/core/Button";

import { connect } from "react-redux";
import { changeValue, addUserProfile } from "../../actions";

function SelectUserForm({
  groups,
  teachers,
  btn_status,
  changeValue,
  addUserProfile,
  handleModalClose,
}) {
  return (
    <div className="card">
      <form id="select-rasp">
        <div
          className="card-title"
          style={{ fontSize: "14px", textAlign: "justify" }}
        >
          👨‍🎓 Начните вводить название группы, если вы студент
        </div>
        <Autocomplete
          id="select-group"
          options={groups.list}
          value={groups.value}
          onChange={(e, newValue) => {
            changeValue(newValue, "group");
          }}
          getOptionLabel={(option) => option}
          renderInput={(params) => <TextField {...params} label="Группа" />}
        />
        <div
          className="card-title"
          style={{ fontSize: "14px", textAlign: "justify", marginTop: "20px" }}
        >
          👨‍🏫 Начните вводить вашу фамилию, если вы преподаватель
        </div>
        <Autocomplete
          id="select-teacher"
          options={teachers.list}
          value={teachers.value}
          onChange={(e, newValue) => {
            changeValue(newValue, "teacher");
          }}
          getOptionLabel={(option) => option}
          renderInput={(params) => (
            <TextField {...params} label="Преподаватель" />
          )}
        />
        <div className="row">
          {!btn_status ? (
            <Button variant="contained" color="primary" disabled>
              Выбрать
            </Button>
          ) : (
            <Button
              variant="contained"
              color="primary"
              onClick={(e) => {
                e.preventDefault();
                const whois =
                  groups.value === "Выбрать группу" ? "teach" : "stud";
                const value =
                  groups.value === "Выбрать группу"
                    ? teachers.value
                    : groups.value;
                addUserProfile(whois, value);
                if (handleModalClose) {
                  handleModalClose();
                }
              }}
            >
              Выбрать
            </Button>
          )}
        </div>
      </form>
    </div>
  );
}

const mapStateToProps = (state) => {
  return {
    groups: state.userState.groupsData,
    teachers: state.userState.teachersData,
    btn_status: state.userState.btn_status,
  };
};

const mapDispatchToProps = { changeValue, addUserProfile };

export default connect(mapStateToProps, mapDispatchToProps)(SelectUserForm);
