import React, { useEffect } from "react";

// import HeaderComponent from "./Header";
// import MotivationComponent from "./Motivation";
// import RaspSliderComponent from "../containers/Slider";
import RaspAutobusComponent from "../containers/RaspAutobus";
// import FooterComponent from "./Footer";
// import SkeletonUserForm from "./RaspSlider/SkeletonUserForm";

import Skeleton from "@material-ui/lab/Skeleton";
import CompareArrowsIcon from "@material-ui/icons/CompareArrows";

import "../styles/IndexPage.sass";

export default function IndexPage({ loading, buse, checkUser }) {
  const user = JSON.parse(localStorage.getItem("user"));

  useEffect(() => {
    checkUser();
  }, [checkUser]);

  return (
    <div id="index-page">
      {/* <HeaderComponent />
      {!loading && !user ? (
        <SkeletonUserForm />
      ) : !loading && user ? (
        <SkeletonUserRasp />
      ) : (
        <RaspSliderComponent />
      )} */}
      {buse ? <RaspAutobusComponent /> : <SkeletonBuseRasp />}

      {/* <FooterComponent /> */}
    </div>
  );
}

function SkeletonBuseRasp() {
  return (
    <div className="rasp-autobus">
      <div className="section-title">Bus schedule</div>

      <div className="profiles" style={{ marginBottom: "15px" }}>
        <div className="items">
          {[6, 14, 29].map((item, i) => (
            <Skeleton variant="rect" key={i} style={{ marginRight: "8px" }}>
              <div className="item">{item.name}</div>
            </Skeleton>
          ))}
        </div>
        <Skeleton variant="rect">
          <div className="edit item">
            <CompareArrowsIcon />
          </div>
        </Skeleton>
      </div>

      <div className="autobuses">
        <Skeleton variant="rect" style={{ width: "100%", margin: "0 auto" }}>
          <div className="route">Маршрут: 6</div>
        </Skeleton>
        {[1, 2].map((item, i) => (
          <div className="direction" key={i}>
            <Skeleton variant="rect">
              <div className="title">
                <CompareArrowsIcon /> Университет - Сквер Героя Карвата
              </div>
            </Skeleton>
            {[1].map((item, k) => (
              <div className="helt" key={k}>
                <Skeleton variant="rect">
                  <div className="title">Остановка: Университет</div>
                </Skeleton>
                <div
                  className="times"
                  style={{ justifyContent: "space-between" }}
                >
                  {[1, 2, 3].map((item, y) => (
                    <Skeleton variant="rect" key={y}>
                      <div className="item">
                        <div className="time">15:23</div>
                        <div className="time-to">через 15 минут</div>
                      </div>
                    </Skeleton>
                  ))}
                </div>
                <Skeleton variant="rect">
                  <div className="full-rasp">Показать полностью...</div>
                </Skeleton>
              </div>
            ))}
          </div>
        ))}
        <Skeleton variant="rect" style={{ maxWidth: "100%" }}>
          <div className="actions">
            <div className="add-direction">Добавить остановку</div>
          </div>
        </Skeleton>
      </div>
    </div>
  );
}

function SkeletonUserRasp() {
  return (
    <div className="rasp-slider">
      <div className="section-title">Timetable</div>

      <div className="profiles" style={{ marginBottom: "15px" }}>
        <div className="items">
          {[6, 14, 29].map((item, i) => (
            <Skeleton variant="rect" key={i} style={{ marginRight: "8px" }}>
              <div className="item">{item.name}</div>
            </Skeleton>
          ))}
        </div>
        <Skeleton variant="rect">
          <div className="edit item">
            <CompareArrowsIcon />
          </div>
        </Skeleton>
      </div>

      <div className="slick-slider" style={{ padding: "0 20px" }}>
        <div className="slide">
          <div className="content">
            <div className="title">
              <Skeleton variant="rect" style={{ margin: "0 auto" }}>
                <div className="date">21 Июля 2021</div>
              </Skeleton>
              <Skeleton
                variant="rect"
                style={{ margin: "0 auto", marginTop: "4px" }}
              >
                <div className="week">Понедельник</div>
              </Skeleton>
            </div>

            {[1, 2, 3, 4].map((time, i) => (
              <div className="slide-row" key={i}>
                <div
                  className="main-info"
                  style={{ display: "flex", marginRight: "10px" }}
                >
                  <div style={{ marginRight: "6px" }}>🕦</div>
                  <Skeleton variant="rect">
                    <div className="time">14:55</div>
                  </Skeleton>
                  <div className="f-right">
                    <div className="badges">
                      <Skeleton variant="rect" style={{ marginRight: "6px" }}>
                        <div className="badge">ЛК</div>
                      </Skeleton>
                    </div>
                    <Skeleton variant="rect">
                      <div className="audience">4/103</div>
                    </Skeleton>
                  </div>
                </div>
                <div className="second-info" style={{ flexGrow: 1 }}>
                  <div style={{ display: "flex" }}>
                    <div style={{ marginRight: "6px" }}>📖</div>
                    <Skeleton variant="rect">
                      <div className="discipline">ОрганПрМенМ/с</div>
                    </Skeleton>
                  </div>
                  <div style={{ display: "flex" }}>
                    <div style={{ marginRight: "6px" }}>👨‍🏫</div>
                    <Skeleton variant="rect">
                      <div className="teacher">Азаров М.Ю.</div>
                    </Skeleton>
                  </div>
                </div>
              </div>
            ))}
          </div>
        </div>
      </div>
    </div>
  );
}
