import React, { useEffect, useState } from "react";
import moment from "moment";
import CompareArrowsIcon from "@material-ui/icons/CompareArrows";
import Button from "@material-ui/core/Button";
import ProfilesComponent from "../Profiles";
import Modal from "@material-ui/core/Modal";
import { makeStyles } from "@material-ui/core/styles";
import ArrowLeftIcon from "@material-ui/icons/ArrowLeft";
import ArrowRightIcon from "@material-ui/icons/ArrowRight";
import CheckCircle from "@material-ui/icons/CheckCircle";
import RadioButtonUnchecked from "@material-ui/icons/RadioButtonUnchecked";

const useStyles = makeStyles((theme) => ({
  paper: {
    position: "absolute",
    bottom: "0",
    width: "100%",
    height: "60%",
    backgroundColor: theme.palette.background.paper,
    boxShadow: theme.shadows[5],
    outline: "none",
    textAlign: "center",
    borderRadius: "15px 15px 0 0",
  },
}));

export default function RaspAutobusComponent({
  buses,
  helts,
  profiles,
  changeActiveProfile,
  addHeltsName,
}) {
  const [open, setOpen] = useState(false);
  const handleOpen = () => setOpen(true);
  const handleClose = () => setOpen(false);
  const [hoursNow, setHoursNow] = useState(moment().format("H"));
  const [minuteNow, setMinuteNow] = useState(moment().format("mm"));
  const [dirBuses, setDirBuses] = useState(0);

  useEffect(() => {
    setInterval(() => {
      setHoursNow(moment().format("H"));
      setMinuteNow(moment().format("mm"));
    }, 1000 * 60);
  }, [dirBuses, hoursNow]);

  return (
    <div className="rasp-autobus">
      <div className="section-title">Bus schedule</div>

      <ProfilesComponent
        type={"buse"}
        profiles={profiles.buse}
        active={profiles.activeBuse}
        changeActiveProfile={changeActiveProfile}
      />

      <div className="autobuses">
        <div className="route">Маршрут: {profiles.activeBuse.name}</div>
        {helts[profiles.activeBuse.name].dir.map((dirNumb, i) => (
          <Direction
            buses={buses}
            helts={helts}
            profiles={profiles}
            dirNumb={dirNumb}
            hoursNow={hoursNow}
            minuteNow={minuteNow}
            dirBuses={dirBuses}
            key={i}
          />
        ))}
        <div className="actions">
          <Button variant="outlined" onClick={handleOpen}>
            Добавить остановку
          </Button>
        </div>
      </div>
      <HeltProfilesModal
        open={open}
        handleClose={handleClose}
        setDirBuses={setDirBuses}
        dirBuses={dirBuses}
        buses={buses}
        helts={helts[profiles.activeBuse.name][dirBuses]}
        profiles={profiles}
        addHeltsName={addHeltsName}
      />
    </div>
  );
}

function Direction({
  buses,
  helts,
  profiles,
  dirNumb,
  hoursNow,
  minuteNow,
  dirBuses,
}) {
  const dirName = buses[profiles.activeBuse.name].direction[dirNumb];
  const heltsArr = helts[profiles.activeBuse.name][dirNumb];

  return (
    <div className="direction">
      <div className="title">
        <CompareArrowsIcon /> {dirName}
      </div>
      {heltsArr.map((helt, k) => (
        <Helt
          buses={buses}
          profiles={profiles}
          dirNumb={dirNumb}
          helt={helt}
          hoursNow={hoursNow}
          minuteNow={minuteNow}
          dirBuses={dirBuses}
          key={k}
        />
      ))}
      {/* <div className="full-rasp">Показать полностью...</div> */}
    </div>
  );
}

function Helt({ buses, profiles, dirNumb, helt, hoursNow, minuteNow }) {
  const [open, setOpen] = useState(false);
  const handleOpen = () => setOpen(true);
  const handleClose = () => setOpen(false);

  const timeArr =
    buses[profiles.activeBuse.name].helts[dirNumb][helt][isWeekend()] || [];

  const takeTime = [];

  for (let time of timeArr) {
    const [hours, minute] = time.split(":");
    if ((+hours === +hoursNow && +minute > +minuteNow) || +hours > +hoursNow) {
      takeTime.push(`${hours}:${minute}`);
    }
  }

  const nearestTime = takeTime.slice(0, 3);

  const timeText = nearestTime.map((item) => {
    let diffHours = calcDiffHours(item, hoursNow, minuteNow);
    let diffMinute = calcDiffMinute(item, hoursNow, minuteNow);

    if (diffHours !== 0) {
      diffMinute -= diffHours * 60;
      return `через ${diffHours}ч ${
        +diffMinute < 10 ? `0${diffMinute}` : diffMinute
      }м`;
    } else {
      return `через ${diffMinute}м`;
    }
  });

  return (
    <React.Fragment>
      <div className="helt" onClick={handleOpen}>
        <div className="title">Остановка: {helt}</div>
        <div className="times">
          {nearestTime.length === 0 ? (
            <div>Автобусов нет</div>
          ) : (
            nearestTime.map((item, y) => (
              <div className={`item c-${nearestTime.length}`} key={y}>
                <div className="time">{item}</div>
                <div className="time-to">{timeText[y]}</div>
              </div>
            ))
          )}
        </div>
      </div>
      <HeltModal
        helt={helt}
        timeArr={timeArr}
        open={open}
        handleClose={handleClose}
        dirBuses={buses[profiles.activeBuse.name].direction[dirNumb]}
        nearestTime={nearestTime}
        hoursNow={hoursNow}
        minuteNow={minuteNow}
      />
    </React.Fragment>
  );
}

function HeltProfilesModal({
  open,
  handleClose,
  setDirBuses,
  dirBuses,
  buses,
  helts,
  profiles,
  addHeltsName,
}) {
  const classes = useStyles();
  const dirName = buses[profiles.activeBuse.name].direction[dirBuses];
  const dirHeltsNames = buses[profiles.activeBuse.name].heltsNames[dirBuses];

  return (
    <Modal open={open} onClose={handleClose}>
      <div className={`${classes.paper} modal`}>
        <div
          className="dir-name"
          onClick={() => setDirBuses(dirBuses === 0 ? 1 : 0)}
        >
          <ArrowLeftIcon className="arrow-left" />
          {dirName}
          <ArrowRightIcon className="arrow-right" />
        </div>
        <div className="content">
          {dirHeltsNames.map((item, y) => {
            const status = helts.indexOf(item) !== -1;
            return (
              <div
                className="row"
                key={y}
                onClick={() =>
                  addHeltsName(item, profiles.activeBuse.name, dirBuses)
                }
              >
                <div className="mx-w">{item}</div>
                {status ? (
                  <CheckCircle style={{ color: "#4caf50" }} />
                ) : (
                  <RadioButtonUnchecked style={{ color: "#ababab" }} />
                )}
              </div>
            );
          })}
        </div>
      </div>
    </Modal>
  );
}

function HeltModal({
  helt,
  timeArr,
  open,
  handleClose,
  dirBuses,
  nearestTime,
  hoursNow,
  minuteNow,
}) {
  const classes = useStyles();

  let timeCounter = 0;
  const timeText = nearestTime.map((item) => {
    let diffHours = calcDiffHours(item, hoursNow, minuteNow);
    let diffMinute = calcDiffMinute(item, hoursNow, minuteNow);

    if (diffHours !== 0) {
      diffMinute -= diffHours * 60;
      return `через ${diffHours}ч ${
        +diffMinute < 10 ? `0${diffMinute}` : diffMinute
      }м`;
    } else {
      return `через ${diffMinute}м`;
    }
  });

  const time_obj = {};
  for (let item of timeArr) {
    if (!time_obj[item.split(":")[0]]) {
      time_obj[item.split(":")[0]] = [item.split(":")[1]];
    } else {
      time_obj[item.split(":")[0]] = [
        ...time_obj[item.split(":")[0]],
        item.split(":")[1],
      ];
    }
  }

  return (
    <Modal open={open} onClose={handleClose}>
      <div className={`${classes.paper} helt-modal`}>
        <div className="direction">
          <span>{helt}</span>
          <br />
          {dirBuses}
        </div>
        <div className="content">
          {Object.entries(time_obj)
            .sort((a, b) => a[0] - b[0])
            .map(([key, value]) => {
              const isActive = nearestTime.filter(
                (time) => time.split(":")[0] === key
              );
              return (
                <div className="hours" key={key}>
                  <div className="title">{key}</div>
                  <div className="items">
                    {value.map((item, y) => {
                      const isActive2 = isActive.filter(
                        (time) => time.split(":")[1] === item
                      );
                      if (isActive.length !== 0 && isActive2.length !== 0) {
                        timeCounter++;
                        return (
                          <div className="item active" key={y}>
                            <div className="time">{item}</div>
                            <div className="time-to">
                              {timeText[timeCounter - 1]}
                            </div>
                          </div>
                        );
                      }
                      return (
                        <div className="item" key={y}>
                          <div className="time">{item}</div>
                        </div>
                      );
                    })}
                  </div>
                </div>
              );
            })}
        </div>
      </div>
    </Modal>
  );
}

function isWeekend() {
  return moment().weekday() === 6 || moment().weekday() === 0 ? 1 : 0;
}

function calcDiffHours(item, hoursNow, minuteNow) {
  const [itemHours, itemMinute] = item.split(":");
  return moment([2007, 0, 29, itemHours, itemMinute, 0]).diff(
    [2007, 0, 29, hoursNow, minuteNow, 0],
    "hours"
  );
}

function calcDiffMinute(item, hoursNow, minuteNow) {
  const [itemHours, itemMinute] = item.split(":");
  return moment([2007, 0, 29, itemHours, itemMinute, 0]).diff(
    [2007, 0, 29, hoursNow, minuteNow, 0],
    "minutes"
  );
}
