import { userConstants } from "../constants";

const initialState = {
  loading: false,
  profiles: {},
  helts: {},
  buse: null,
  user: null,
  raspData: null,
  nextweekData: { data: [] },
  groupsData: {},
  teachersData: {},
  btn_status: false,
  dayofweek: 0,
};

export function userState(state = initialState, action) {
  switch (action.type) {
    case userConstants.SET_INIT_STATE:
      return {
        ...state,
        loading: true,
        user: action.payload.user,
        profiles: action.payload.profiles,
        helts: action.payload.helts,
        buse: action.payload.buse,
        groupsData: action.payload.groupsData,
        teachersData: action.payload.teachersData,
        dayofweek: action.payload.dayofweek,
      };
    case userConstants.ADD_USER_PROFILE:
      return {
        ...state,
        user: action.payload.user,
        profiles: action.payload.profiles,
        groupsData: {
          ...state.groupsData,
          value: "Выбрать группу",
        },
        teachersData: {
          ...state.teachersData,
          value: "Выбрать преподавателя",
        },
        btn_status: false,
        loading: false,
      };
    case userConstants.SET_GROUP_VALUE:
      return {
        ...state,
        groupsData: {
          ...state.groupsData,
          value: action.payload.value,
        },
        teachersData: {
          ...state.teachersData,
          value: state.teachersData.list[0],
        },
        btn_status: action.payload.btn,
      };
    case userConstants.SET_TEACHER_VALUE:
      return {
        ...state,
        teachersData: {
          ...state.teachersData,
          value: action.payload.value,
        },
        groupsData: {
          ...state.groupsData,
          value: state.groupsData.list[0],
        },
        btn_status: action.payload.btn,
      };
    case userConstants.SET_ACTIVE_BUSE:
      return {
        ...state,
        profiles: {
          ...state.profiles,
          activeBuse: { name: action.payload },
        },
      };
    case userConstants.SET_ACTIVE_RASP:
      return {
        ...state,
        profiles: {
          ...state.profiles,
          activeRasp: { type: action.payload.type, name: action.payload.name },
        },
        loading: false,
      };
    case userConstants.SET_REQWEEK_DATA:
      return {
        ...state,
        raspData: action.payload.raspData,
        dayofweek: action.payload.dayofweek,
        loading: action.payload.loading,
      };
    case userConstants.SET_NEXTWEEK_RASP_STATUS:
      return {
        ...state,
        nextweekData: action.payload,
      };
    case userConstants.SET_NEXT_WEEK_DATA:
      return {
        ...state,
        raspData: action.payload,
        dayofweek: "1",
      };
    case userConstants.DELETE_USER_PROFILE:
      return {
        ...state,
        profiles: action.payload.profiles,
        loading: action.payload.loading,
      };
    case userConstants.ADD_HELTS_NAME:
      return {
        ...state,
        helts: action.payload,
      };
    default:
      return state;
  }
}
