import { connect } from "react-redux";
import { changeActiveProfile, addHeltsName } from "../actions";
import RaspAutobus from "../components/RaspAutobus";

const mapStateToProps = (state) => {
  return {
    buses: state.userState.buse,
    helts: state.userState.helts,
    profiles: state.userState.profiles,
  };
};

const mapDispatchToProps = { changeActiveProfile, addHeltsName };

export default connect(mapStateToProps, mapDispatchToProps)(RaspAutobus);
